*{
  padding: 0;
  margin: 0;
  border: 0;
  font-size: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
}
.container{
  width: 100% ;
  display: flex;
  flex-direction: row;
  height: 100vh;
}
.menu{
  width: 15%;
  padding: 10px 0px 0 0px;

}
.menu .top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-left{
  padding-left: 10px;
}
.menu .top-menu span{
  display:flex;
  align-items: center;
}
.logo{
  margin-right: 10px;
  width: 40px;
}
.icon-menu{
  margin-right: 20px;
}
.slogan{
  font-size: 18px;
  font-weight: bold;
}
.catalog{
  margin: 20px 0 0 0;
}
ul{
  list-style-type: none;
}
.catalog li{
  font-weight: bold;
  padding: 10px 0 10px 15px;
}
.catalog li span{
  margin-left: 15px;
}
.fa-icon-House{
  color: #00a8ff;
}
.fa-icon-Main{
  color: #e74c3c;
}
.fa-icon-Chart{
  color: #f1c40f;
}
.fa-icon-Logs{
  color: #FC427B;
}
/* Content is right */
.content{
  background-color: rgb(242, 239, 239);
  width: 85%;
  display: flex;
  flex-direction: column;
  border-left: 1px solid rgba(211, 211, 211, 0.7);
}
.content-header{
  padding: 10px 20px 0 20px;
  height: 35%;
  background-image: linear-gradient(to right, #38aafb, #043dfaf0);
}
.Dashboard{
  color: white;
  display: flex;
  flex-direction: column;
}
.title-content{
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.title {
  font-size: 24px;
  margin-bottom: 5px;
  color: white;
}
.profile{
  color: white;
  display: flex;
  align-items: center;
}
.profile span{
  font-size: 14px;
  font-weight: 600;
  padding-right: 15px;
  padding-left: 15px;
}
.avatar{
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}
.Dashboard span {
  margin-bottom: 10px;
  font-weight: bold;
}